@use 'base'
    
// Logo
.logo
    height: 3.25rem
    margin-left: 2rem


// Search Box
.search
    flex: 0 0 40%
    display: flex
    justify-content: center
    align-items: center

    @media only screen and (max-width: base.$bp-smallest)
        order: 1
        flex: 0 0 100%
        background-color: var(--color-grey-light-3)

    &__input
        font-family: inherit
        font-size: inherit
        color: var(--color-grey-dark-3)
        background-color: var(--color-grey-light-3)
        border: none
        padding: 0.7rem 2rem
        border-radius: 10rem
        width: 90%
        margin-right: -3.5rem

        &:focus
            outline: none
            width: 100%
            background-color: var(--color-grey-light-3)
            transition: all 0.2s

        &::placeholder
            font-weight: 100
            color: var(--color-grey-dark-1)

    &__input:focus + &__button
        background-color: var(--color-grey-light-3)

    &__button
        border: none
        background-color: var(--color-grey-light-2)

        &:focus
            outline: none
            transform: translateY(2px)
    
    &__icon
        height: 2rem
        width: 2rem
        fill: var(--color-grey-dark-3)


// User Nav

.user-nav
    align-self: stretch
    display: flex
    flex: 0 0 20%
    justify-content: space-between
    align-items: center

    & > *
        padding: 0 2rem
        cursor: pointer
        height: 100%
        display: flex
        align-items: center

    & > *:hover
        background-color: var(--color-grey-light-2)

    &__icon-box
        position: relative
    
    &__icon
        height: 2.25rem
        width: 2.25rem
        fill: var(--color-grey-dark-2)

    &__notification
        font-size: 0.8rem
        height: 1.75rem
        width: 1.75rem
        border-radius: 50%
        background-color: var(--color-primary)
        color: #fff
        position: absolute
        top: 1.5rem
        right: 1.1rem
        display: flex
        justify-content: center
        align-items: center

    &__user-photo
        height: 3.25rem
        width: 3.25rem
        border-radius: 50%
        margin-right: 1rem

// Side Navigation

.side-nav
    font-size: 1.4rem
    list-style: none
    margin-top: 3.5rem

    @media only screen and (max-width: base.$bp-medium)
        display: flex
        margin-top: 0

    &__item
        position: relative
        
        @media only screen and (max-width: base.$bp-medium)
            flex: 1

        &:not(:last-child)
            margin-bottom: 0.5rem

            @media only screen and (max-width: base.$bp-medium)
                margin: 0

    &__item::before
        content: ''
        position: absolute
        top: 0
        left: 0
        height: 100%
        width: 3px
        background-color: var(--color-primary)
        transform: scaleY(0)
        transition: transform 0.2s, width 0.4s cubic-bezier(1,0,0,1) 0.2s, background-color 0.1s

    &__item:hover::before, &__item--active::before
        transform: scaleY(1)
        width: 100%

    &__item:active:before 
        background-color: var(--color-primary-light)

    &__link:link, &__link:visited
        color: var(--color-grey-light-1)
        text-decoration: none
        text-transform: uppercase
        padding: 1.5rem 3rem
        display: flex
        align-items: center
        position: relative
        z-index: 1

        @media only screen and (max-width: base.$bp-medium)
            justify-content: center
            padding: 2rem

        @media only screen and (max-width: base.$bp-small)
            flex-direction: column
            padding: 1rem 0.5rem

    &__icon
        width: 1.75rem
        height: 1.75rem
        fill: currentColor
        margin-right: 2rem

        @media only screen and (max-width: base.$bp-small)
            margin-right: 0
            margin-bottom: 0.5rem
            width: 1.5rem
            height: 1.5rem

.legal
    font-size: 1.2rem
    text-align: center
    padding: 2.5rem
    color: var(--color-grey-light-4)

    @media only screen and (max-width: base.$bp-medium)
        display: none

// Gallery

.gallery
    display: flex

    &__photo
        width: 100%
        display: block

// Overview

.overview
    display: flex
    align-items: center
    border-bottom: var(--line)
    background-color: #fff

    @media only screen and (max-width: base.$bp-small)
        font-size: 1.8rem

    &__heading
        font-size: 2.25rem
        font-weight: 300
        text-transform: uppercase
        letter-spacing: 1px
        padding: 1.5rem 3rem

        @media only screen and (max-width: base.$bp-small)
            font-size: 1.6rem

    &__stars
        margin-right: auto
        display: flex

    &__icon-star, &__icon-location
        height: 1.75rem
        width: 1.75rem
        fill: var(--color-primary)

    &__location
        font-size: 1.2rem
        display: flex
        align-items: center

        @media only screen and (max-width: base.$bp-small)
            flex-direction: column

    &__icon-location
        margin-right: 0.5rem

        @media only screen and (max-width: base.$bp-small)
            margin-right: 0
            margin-bottom: 0.5rem

    &__rating
        background-color: var(--color-primary)
        margin-left: 3rem
        color: #fff
        align-self: stretch
        padding: 0 2.25rem
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center

        @media only screen and (max-width: base.$bp-small)
            padding: 0 2rem

    &__rating-average
        font-size: 2.25rem
        font-weight: 300
        margin-bottom: -3px

        @media only screen and (max-width: base.$bp-small)
            font-size: 1.8rem

    &__rating-count
        font-size: 0.8rem
        text-transform: uppercase

        @media only screen and (max-width: base.$bp-small)
            font-size: 0.5rem
  
// Button inline
.btn-inline
    border: none
    color: var(--color-primary)
    font-size: inherit
    border-bottom: 1px solid currentColor
    padding-bottom: 2px
    display: inline-block
    background-color: transparent
    cursor: pointer
    transition: all 0.2s

    & span
        margin-left: 0.3rem
        transition: margin-left 0.2s

    &:hover
        color: var(--color-grey-dark-1)

        & span
            margin-left: 0.8rem

    &:focus
        outline: none
        animation: pulsate 1s infinite


@keyframes pulsate
    0%
        transform: scale(1)
        box-shadow: none

    50%
        transform: scale(1.05)
        box-shadow: 0 1rem 4rem rgba(black, 0.25)

    100%
        transform: scale(1)
        box-shadow: none

// Detail
// Paragraph
.paragraph:not(:last-of-type)
    margin-bottom: 2rem

// List
.list
    list-style: none
    margin: 3rem 0
    padding: 3rem 0
    border-top: var(--line)
    border-bottom: var(--line)
    display: flex
    flex-wrap: wrap

    &__item
        flex: 0 0 50%
        margin-bottom: 0.7rem

    &__item::before
        content: ''
        display: inline-block
        height: 1rem
        width: 1rem
        margin-right: 0.7rem
        background-image: url(../img/chevron-thin-right.svg)
        background-size: cover

        @supports (mask-image: url()) or (-webkit-mask-image: url())
            background-color: var(--color-primary)
            mask-image: url(../img/chevron-thin-right.svg)
            -webkit-mask-image: url(../img/chevron-thin-right.svg)
            mask-size: cover
            -webkit-mask-size: cover
            background-image: none
        

// Recommend

.recommend
    font-size: 1.3rem
    color: var(--color-grey-dark-3)
    display: flex
    align-items: center

    &__friends
        display: flex
    
    &__photo
        box-sizing: content-box
        height: 4rem
        width: 4rem
        border-radius: 50%
        border: 3px solid #fff
        
        &:not(:last-child)
            margin-right: -2rem

    &__count
        margin-right: auto


// Reviews

.review
    background-color: #fff
    box-shadow: var(--box-shadow-light)
    padding: 3rem
    margin-bottom: 3.5rem
    display: flex
    flex-direction: column
    justify-content: space-between
    position: relative

    @media only screen and (max-width: base.$bp-medium)
        padding: 2rem
        margin-bottom: 3rem

    &__text
        z-index: 2
        position: relative
        overflow: hidden

    &__user
        display: flex
        align-items: center
        margin-top: 2rem

    &__photo
        height: 4.5rem
        width: 4.5rem
        border-radius: 50%
        margin-right: 1.5rem

    &__user-box
        margin-right: auto
        display: flex
        flex-direction: column
        justify-content: space-between

    &__user-name
        color: var(--color-grey-dark-2)
        font-size: 1.1rem
        font-weight: 600
        text-transform: uppercase

    &__user-date
        font-weight: 300
        font-size: 1rem
        margin-top: 0.4rem
        color: var(--color-grey-dark-3)

    &__rating
        font-size: 2.2rem
        color: var(--color-primary)
        font-weight: 300

    &::before
        content: "\201C"
        position: absolute
        top: -2.75rem
        left: -1rem
        font-size: 20rem
        color: var(--color-grey-light-2)
        font-family: sans-serif
        line-height: 1
        z-index: 1

// CTA
.cta
    padding: 3.5rem 0
    text-align: center
    background-color: #fff

    @media only screen and (max-width: base.$bp-medium)
        padding: 2.5rem 0

    &__book-now
        font-size: 2rem
        text-transform: uppercase
        font-weight: 300
        margin-bottom: 2.5rem

// CTA Button
.btn
    font-size: 1.5rem
    border: none
    font-weight: 300
    text-transform: uppercase
    border-radius: 10rem
    background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark))
    color: #fff
    position: relative
    overflow: hidden

    & > *
        display: inline-block
        height: 100%
        width: 100%
        transition: all 0.2s

    &__visible
        padding: 2rem 7.5rem

    &__invisible
        padding: 2rem 0
        position: absolute
        left: 0
        top: -100%

    &:hover
        background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark))
        
    &:hover &__invisible
        top: 0

    &:hover &__visible
        transform: translateY(100%)

    &:focus
        outline: none
        animation: pulsate 1s infinite